/** @jsx jsx */
import { css, jsx } from "@emotion/react"
import { swapTheme } from "../common/theme"
import {Light} from '../images/Light'

const headingStyles = css`
  margin: 0;
  max-width: 320px;
  font-variant: petite-caps;
`

const headerWrapperStyle = css`
    margin-bottom: 20px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    gap: 10px;
`

const headingRightSideStyle = css`
    display: flex;
    gap: 20px;
`

const navStyle = css`
    display: flex;
    gap: 20px;
    padding: 0px;

`

export function Header() {
    const isHome = typeof window !== 'undefined' && window.location.pathname === '/'
    return <div css={headerWrapperStyle}>      
        <h1 css={headingStyles}>
        Joseph D. Phelan
        </h1>
        <div css={headingRightSideStyle}>
            <nav>
                <ul css={navStyle}>
                    <li>
                        <a href="/" className={isHome ? 'active' : ''}>Home</a>
                    </li>
                    <li>
                        <a href="/aboutMe" className={!isHome ? 'active' : ''}>About me</a>
                    </li>
                </ul>
            </nav>
            <button onClick={() => swapTheme()} aria-label="toggle theme">
                <Light fill={'var(--fg-color)'}/>
            </button>
        </div>
    </div>
}